/* Testimonials Section Responsive Styles */

.testimonials-section {
  background: linear-gradient(180deg, #70A6B2 0%, #70A6B2 50%, white 50%, white 100%);
  padding-top: 32px;
  padding-bottom: 32px;
  position: relative;
}

.testimonials-container {
  max-width: 1440px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

.testimonials-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 500;
  color: white;
  margin-bottom: 24px;
  padding-left: 16px;
  padding-right: 16px;
}

.testimonials-toggle-container {
  display: flex;
  justify-content: center;
  margin-bottom: 32px;
}

.testimonials-toggle-box {
  background: white;
  border-radius: 25px;
  padding: 8px;
  display: flex;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  width: 90%;
  max-width: 300px;
}

.testimonials-toggle-button {
  border-radius: 20px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: none;
  flex: 1;
}

.testimonials-video-container {
  position: relative;
  width: 100%;
}

.testimonials-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  background: white;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  width: 40px;
  height: 40px;
}

.testimonials-arrow-left {
  left: -5px;
}

.testimonials-arrow-right {
  right: -5px;
}

.testimonials-arrow-icon {
  font-size: 1rem;
}

.testimonials-video-scroll {
  display: flex;
  gap: 16px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
  scroll-padding-left: 20px;
  padding-left: 24px;
  padding-right: 24px;
  align-items: stretch;
  min-height: 450px;
  -webkit-overflow-scrolling: touch;
}

.testimonials-video-scroll::-webkit-scrollbar {
  display: none;
}

.testimonials-video-card {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 8px 30px rgba(0,0,0,0.12);
  transition: all 0.3s ease;
  cursor: pointer;
  min-width: 280px;
  max-width: 280px;
  height: 100%;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  outline: none;
  padding: 0;
  margin: 0;
}

.testimonials-video-content {
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.testimonials-video-name {
  font-weight: 600;
  color: #2C3E50;
  margin-bottom: 8px;
  font-size: 1rem;
}

.testimonials-video-role {
  color: #7F8C8D;
  font-size: 0.8rem;
  margin-bottom: 4px;
}

.testimonials-video-university {
  color: #70A6B2;
  font-size: 0.75rem;
  font-weight: 500;
}

.testimonials-play-button {
  font-size: 2.5rem;
  color: white;
}

.testimonials-placeholder {
  width: 100%;
  height: 180px;
  background-color: #70A6B2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  padding: 16px;
}

.testimonials-written-grid {
  width: 100%;
}

.testimonials-written-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 360px;
}

.testimonials-counter {
  text-align: center;
  margin-top: 24px;
}

.testimonials-dialog-content {
  padding: 16px;
}

/* Media Queries */

/* Small devices (640px and above) */
@media (min-width: 640px) {
  .testimonials-section {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  .testimonials-container {
    padding-left: 24px;
    padding-right: 24px;
  }

  .testimonials-title {
    font-size: 2rem;
    margin-bottom: 32px;
    padding-left: 0;
    padding-right: 0;
  }

  .testimonials-toggle-container {
    margin-bottom: 40px;
  }

  .testimonials-toggle-box {
    width: auto;
    max-width: none;
  }

  .testimonials-toggle-button {
    flex: none;
    font-size: 1rem;
  }

  .testimonials-arrow {
    width: 50px;
    height: 50px;
  }

  .testimonials-arrow-left {
    left: -15px;
  }

  .testimonials-arrow-right {
    right: -15px;
  }

  .testimonials-arrow-icon {
    font-size: 1.2rem;
  }

  .testimonials-video-scroll {
    gap: 24px;
    scroll-padding-left: 32px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .testimonials-video-card {
    min-width: 320px;
    max-width: 320px;
  }

  .testimonials-video-content {
    padding: 24px;
  }

  .testimonials-video-name {
    font-size: 1.125rem;
  }

  .testimonials-video-role {
    font-size: 0.875rem;
  }

  .testimonials-play-button {
    font-size: 3rem;
  }

  .testimonials-placeholder {
    height: 200px;
  }

  .testimonials-written-card {
    height: 380px;
  }

  .testimonials-counter {
    margin-top: 32px;
  }

  .testimonials-dialog-content {
    padding: 24px;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .testimonials-section {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .testimonials-container {
    padding-left: 32px;
    padding-right: 32px;
  }

  .testimonials-title {
    font-size: 2.5rem;
  }

  .testimonials-toggle-container {
    margin-bottom: 48px;
  }

  .testimonials-video-scroll {
    gap: 80px;
    padding-left: 240px;
  }

  .testimonials-written-card {
    height: 400px;
  }
}

/* Large Desktop (1024px and above) */
@media (min-width: 1024px) {
  .testimonials-section {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .testimonials-container {
    padding-left: 48px;
    padding-right: 48px;
  }

  .testimonials-title {
    font-size: 3rem;
  }
}

