/* How It Works Section Responsive Styles */

.how-it-works-section {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  background: linear-gradient(to bottom, #70A6B2 0%, #70A6B2 50%, white 50%, white 100%);
  position: relative;
  min-height: auto;
  padding-top: max(16px, calc(env(safe-area-inset-top) + 16px));
  padding-bottom: max(16px, calc(env(safe-area-inset-top) + 16px));
}

.how-it-works-title {
  align-self: stretch;
  text-align: center;
  color: white;
  font-family: Neutrif Studio;
  font-weight: 500;
  word-wrap: break-word;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 24px;
  font-size: clamp(28px, 7vw, 32px);
}

.how-it-works-steps-container {
  width: 100%;
  max-width: 1400px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
  position: relative;
  top: 0;
  z-index: 10;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  scroll-snap-type: x mandatory;
  scrollbar-width: none;
  margin: 0 auto;
  flex: 1;
  padding-left: 16px;
  padding-right: 16px;
  margin-bottom: 24px;
}

.how-it-works-steps-container::-webkit-scrollbar {
  display: none;
}

.how-it-works-step-card {
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 16px;
  background: white;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 16px;
  display: flex;
  width: clamp(220px, 70vw, 240px);
  max-width: 240px;
  height: auto;
  min-height: 180px;
  flex: 0 0 auto;
  scroll-snap-align: start;
  position: relative;
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 20px;
}

.how-it-works-step-number {
  position: absolute;
  top: 12px;
  right: 12px;
  color: #5C6163;
  font-size: 36px;
  font-family: Neutrif Studio;
  font-weight: 700;
  opacity: 0.15;
  z-index: 2;
  line-height: 1;
}

.how-it-works-step-icon-container {
  width: 50px;
  height: 50px;
  position: relative;
  flex-shrink: 0;
}

.how-it-works-step-icon-bg {
  width: 50px;
  height: 50px;
  left: 0;
  top: 0;
  position: absolute;
  background: white;
  border-radius: 4px;
}

.how-it-works-step-icon-img {
  width: 35px;
  height: 40px;
  left: 4px;
  top: 4px;
  position: absolute;
  object-fit: contain;
}

.how-it-works-step-title {
  margin-bottom: 8px;
  color: #495052;
  font-size: 18px;
  font-family: Neutrif Studio;
  font-weight: 500;
  word-wrap: break-word;
}

.how-it-works-step-description {
  width: 100%;
  color: #5C6163;
  font-size: 14px;
  font-family: Neutrif Studio;
  font-weight: 400;
  line-height: 20px;
  word-wrap: break-word;
}

.how-it-works-step-button {
  flex: 1;
  justify-content: flex-end;
  display: flex;
  flex-direction: column;
  color: rgba(92, 97, 99, 0.40);
  font-size: 14px;
  font-family: Neutrif Studio;
  font-weight: 500;
  word-wrap: break-word;
  cursor: pointer;
}

.how-it-works-step-button:hover {
  color: #495052;
}

.how-it-works-step-actions {
  justify-content: flex-start;
  align-items: center;
  gap: 32px;
  display: flex;
  width: 100%;
}

/* Media Queries */

/* Small devices (640px and above) */
@media (min-width: 640px) {
  .how-it-works-section {
    min-height: 350px;
    padding-top: 48px;
    padding-bottom: 48px;
  }

  .how-it-works-title {
    font-size: clamp(32px, 5.5vw, 40px);
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 32px;
  }

  .how-it-works-steps-container {
    gap: 24px;
    padding-left: 24px;
    padding-right: 24px;
  }

  .how-it-works-step-card {
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 24px;
    gap: 24px;
    width: 260px;
    max-width: 260px;
    min-height: 200px;
    margin-left: 0;
    margin-right: 0;
  }

  .how-it-works-step-number {
    top: 16px;
    right: 16px;
    font-size: 42px;
  }

  .how-it-works-step-icon-container {
    width: 60px;
    height: 60px;
  }

  .how-it-works-step-icon-bg {
    width: 60px;
    height: 60px;
  }

  .how-it-works-step-icon-img {
    width: 45px;
    height: 50px;
    left: 6px;
    top: 6px;
  }

  .how-it-works-step-title {
    font-size: 20px;
  }

  .how-it-works-step-description {
    font-size: 16px;
    line-height: 22px;
  }

  .how-it-works-step-button {
    font-size: 15px;
  }

  .how-it-works-step-actions {
    gap: 48px;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .how-it-works-section {
    min-height: 400px;
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .how-it-works-title {
    font-size: clamp(36px, 3.8vw, 48px);
    padding-bottom: 40px;
  }

  .how-it-works-steps-container {
    flex-direction: row;
    justify-content: center;
    top: -20px;
    flex-wrap: wrap;
    overflow-x: visible;
    scroll-snap-type: none;
    scrollbar-width: auto;
    padding-left: 0;
    padding-right: 0;
  }

  .how-it-works-steps-container::-webkit-scrollbar {
    display: initial;
  }

  .how-it-works-step-card {
    width: 280px;
    max-width: 280px;
    flex: 0 0 280px;
    scroll-snap-align: none;
    margin-left: 0;
    margin-right: 0;
  }

  .how-it-works-step-number {
    font-size: 48px;
  }

  .how-it-works-step-description {
    font-size: 18px;
    line-height: 24px;
  }

  .how-it-works-step-button {
    font-size: 16px;
  }
}

/* Large Desktop (1024px and above) */
@media (min-width: 1024px) {
  .how-it-works-title {
    font-size: clamp(44px, 3.2vw, 60px);
    padding-bottom: 48px;
  }

  .how-it-works-steps-container {
    top: -40px;
  }

  .how-it-works-step-icon-container {
    width: 65px;
    height: 65px;
  }

  .how-it-works-step-icon-img {
    width: 55px;
    height: 55px;
    left: 10px;
  }
}

